import logo from './Holberton_logo.jpg';
import {StyleSheet, css} from "aphrodite"
import React, { useContext } from 'react';
import { AppContext } from '../App/AppContext';

const style = StyleSheet.create({
  appHeader: {
    display: "flex",
    width: "10vw",
    position: "absolute",
  },
  logo: {
    width: "150px",
    height: "150px",
  },
  headerText: {
    display: "flex",
    color: "crimson",
    alignItems: "center",
  }
});
function Header() {
  const {user, logOut} = useContext(AppContext)
  return (
    <div className={css(style.appHeader)}>
      <img src={logo} alt='Holbertonlogo' className={css(style.logo)} />
      <h1 className={css(style.headerText)}>School dashboard</h1>
      {user.isLoggedIn && <section id='logoutSection'>
      <h2>Welcome <strong>{user.email}</strong> <em><p onClick={() => logOut}>(logout)</p></em></h2>
      </section>}
    </div>
  );
}
export default Header;